  width: 100%;
  height: 35px;
  background-color: #fff;
  color: #002266;
}
textarea {
  width: 100%;
  height: 50%;
  border: none;
  text-align: center;
  font-family: 'Arial';
  font-size: 16px;
  line-height: 24px;
  resize: none;
}
textarea:focus, textarea:active {
    outline: none;
}
textarea:disabled {
    color: #000;
}
